.movie-row-container {
    display: flex;
    align-items: flex-start;
    height: 300px;
    width: 1552px;
    position: absolute;
    top: 49px;
    left: 0;
    padding: 0;
    gap: 13px;
    overflow-x: scroll;
    overflow-y: hidden;
}

.movie-row-container::-webkit-scrollbar {
    display: none;
}

.movie-frame {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 300px;
    width: 300px;
    border-radius: 12px;
    gap: 10px;
    position: relative;
    cursor: pointer;
    transition: transform 0.2s;
}

.movie-frame:hover {
    cursor: pointer;
    transform: scale(0.95);
}

.movie-image {
    width: 300px;
    height: 300px;
    border-radius: 12px;
}

.movie-header {
    color: #000000;
    font-weight: 400;
    font-size: 24px;
    position: absolute;
    left: 10px;
    top: 10px;
}

.movie-frame:hover .overlay-container {
    opacity: 1;
}

.overlay-container {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    background: rgba(0, 0, 0, 0.8);
    width: 100%;
    transition: 0.5s ease;
    opacity: 0;
    bottom: 0;
    font-size: 20px;
    padding: 20px;
    text-align: center;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
}

/* Media Query iPad Pro 11 */
@media screen and (max-width: 834px) {
    .movie-row-container {
        display: flex;
        align-items: flex-start;
        height: 300px;
        width: 1552px;
        position: absolute;
        top: 49px;
        left: 0;
        padding: 0;
        gap: 13px;
        overflow-x: scroll;
        overflow-y: hidden;
    }
}

/* Media Query Mobile Devices */
@media only screen and (min-width: 321px) and (max-width: 679px) {
    .movie-row-container {
        display: flex;
        align-items: flex-start;
        height: 200px;
        width: 626px;
        position: absolute;
        top: 49px;
        left: 0;
        padding: 0;
        gap: 13px;
        overflow-x: scroll;
        overflow-y: hidden;
    }

    .movie-frame {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        height: 200px;
        width: 200px;
        border-radius: 12px;
        gap: 10px;
        position: relative;
        cursor: pointer;
    }

    .movie-image {
        width: 200px;
        height: 200px;
        border-radius: 12px;
    }

    .overlay-container {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        background: rgba(0, 0, 0, 0.8);
        width: 100%;
        transition: 0.5s ease;
        opacity: 0;
        bottom: 0;
        font-size: 20px;
        padding: 10px;
        text-align: center;
        border-bottom-left-radius: 12px;
        border-bottom-right-radius: 12px;
    }
}